'use client';
import * as React from 'react';
import LocalizedLink from './localizedLink';
const isHash = (str)=>/^#/.test(str);
const isInternal = (to)=>/^\/(?!\/)/.test(to);
const isStatic = (to)=>/^\/static/.test(to);
const MdxLink = ({ href, ...props })=>isHash(href) || !isInternal(href) || isStatic(href) ? React.createElement("a", {
        href: href,
        target: "_blank",
        rel: "noopener noreferrer",
        ...props
    }) : React.createElement(LocalizedLink, {
        ...props,
        to: href
    });
export default MdxLink;
